<template>
  <div class="replyList">
    <Crumbs
      :parent-manage="'批复管理'"
      :children-manage="'批复列表'"
      @isShowScreen="isShowScreen"
    ></Crumbs>
    <!-- 筛选内容 -->
    <Content
      v-if="isScreen"
      class="content screenStyle"
      :style="{padding: '24px', background: '#fff', marginBottom: '10px'}"
    >
      <Form
        :model="screen"
        :label-width="62"
      >
        <Row type="flex" justify="space-between">
          <Col span="7">
          <FormItem label="表单名称">
            <Input
              v-model="screen.name"
              placeholder="请填写表单名称"
            ></Input>
          </FormItem>
          </Col>
          <Col span="7">
          <FormItem label="表单类别">
            <Select
              v-model="screen.form"
              filterable
            >
              <Option value="beijing">New York</Option>
              <Option value="shanghai">London</Option>
              <Option value="shenzhen">Sydney</Option>
            </Select>
          </FormItem>
          </Col>
          <Col span="7">
          <FormItem label="资金类别">
            <Select
              v-model="screen.ziJin"
              filterable
            >
              <Option value="beijing">New York</Option>
              <Option value="shanghai">London</Option>
              <Option value="shenzhen">Sydney</Option>
            </Select>
          </FormItem>
          </Col>
        </Row>
        <Row type="flex" justify="end" style="margin-top:15px;">
          <Col
            style="text-align: right; margin-right:8px;"
            span="1"
          >
          <Button type="primary" @click="handleScreen">确认</Button>
          </Col>
        </Row>
      </Form>
    </Content>
    <!-- /筛选内容 -->

    <!-- table-content -->
    <Content
      class="content"
      :style="{padding: '14px 24px', minHeight: '220px', background: '#fff'}"
    >
      <Button
        type="primary"
        icon="md-add"
        @click="handleAdd"
      >
        新增
      </Button>
      <Button
        type="primary"
        icon="ios-create"
        @click="handleModify"
      >
        修改
      </Button>
      <Button
        type="primary"
        icon="md-close"
      >
        删除
      </Button>
      <Button
        icon="md-print"
        @click="handlePrint"
      >
        打印
      </Button>
      <Button
        icon="md-exit"
        @click="handleExport"
      >
        导出
      </Button>
      <Table
        ref="tableContent"
        highlight-row
        class="table"
        :columns="columns"
        :data="tableContent"
        style="margin-top: 15px;"
        @on-current-change="selectRow"
      ></Table>
      <!-- 分页组件 -->
      <Page
        style="text-align: right;margin-top: 15px;"
        :total="100"
        show-sizer
      />
      <!-- /分页组件 -->

      <!-- 弹出框 -->
      <Modal
        v-model="selTypeMod"
        class="selStyle specialWidth"
        :mask-closable="false"
      >
        <p slot="header">
          <span v-if="!isDisabled">请添加表单信息</span>
          <span v-else>表单信息</span>
        </p>
        <Tabs
          :animated="false"
          class="innerTabStyle"
          value="name1"
        >
          <TabPane
            label="信息设置"
            name="name1"
          >
            <Form
              ref="formValidateType"
              :model="formValidateType"
              :rules="ruleValidateType"
              :label-width="80"
            >
              <Row>
                <Col span="11">
                <FormItem
                  label="表单类型"
                  prop="name"
                >
                  <Select
                    v-model="formValidateType.quality"
                    :disabled="isDisabled"
                    :transfer="transferValue"
                    filterable
                    placeholder="请选择表单类型"
                  >
                    <Option value="0">政府机关</Option>
                    <Option value="1">事业法人</Option>
                    <Option value="2">合作组织</Option>
                    <Option value="3">企业法人</Option>
                  </Select>
                </FormItem>
                </Col>
                <Col
                  span="11"
                  offset="2"
                >
                <FormItem
                  label="资金类别"
                  prop="quality"
                >
                  <Select
                    v-model="formValidateType.quality"
                    :disabled="isDisabled"
                    :transfer="transferValue"
                    filterable
                    placeholder="请选择机构性质"
                  >
                    <Option value="0">政府机关</Option>
                    <Option value="1">事业法人</Option>
                    <Option value="2">合作组织</Option>
                    <Option value="3">企业法人</Option>
                  </Select>
                </FormItem>
                </Col>
              </Row>
              <Col span="24">
              <FormItem
                label="表单名称"
                prop="quality"
              >
                <Input
                  v-model="formValidateType.remark"
                  :disabled="isDisabled"
                  placeholder="请填写表单名称"
                ></Input>
              </FormItem>
              </Col>
              <Col span="24">
              <FormItem label="使用说明">
                <Input
                  v-model="formValidateType.remark"
                  :disabled="isDisabled"
                  type="textarea"
                  :autosize="{minRows: 1,maxRows: 5}"
                  placeholder="请填写使用说明"
                ></Input>
              </FormItem>
              </Col>
            </Form>
          </TabPane>
          <TabPane
            label="PDF附件"
            name="name7"
          >
            <Icon
              v-if="!isDisabled"
              size="16"
              type="md-alert"
            />
            <b v-if="!isDisabled">请上传与设置信息一致的附件或证明性文件，本系统只接受PDF格式文件，请上传前进行确认。</b>
            <Upload
              v-if="!isDisabled"
              action="//jsonplaceholder.typicode.com/posts/"
            >
              <Button
                class="uploadBtn"
                style="border: none;color: #2b85e4"
                icon="ios-cloud-upload-outline"
              >
                点击此处上传
              </Button>
            </Upload>
            <b
              v-else
              style="margin-bottom: 10px;display: inline-block;"
            >
              <Icon
                size="16"
                type="md-alert"
              />未上传相关附件</b>
          </TabPane>
          <TabPane
            label="WORD附件"
            name="name8"
          >
            <Icon
              v-if="!isDisabled"
              size="16"
              type="md-alert"
            />
            <b v-if="!isDisabled">请上传与设置信息一致的附件或证明性文件，本系统只接受WORD格式文件，请上传前进行确认。</b>
            <Upload
              v-if="!isDisabled"
              action="//jsonplaceholder.typicode.com/posts/"
            >
              <Button
                class="uploadBtn"
                style="border: none;color: #2b85e4"
                icon="ios-cloud-upload-outline"
              >
                点击此处上传
              </Button>
            </Upload>
            <b
              v-else
              style="margin-bottom: 10px;display: inline-block;"
            >
              <Icon
                size="16"
                type="md-alert"
              />未上传相关附件</b>
          </TabPane>
        </Tabs>
        <p
          slot="footer"
          style="text-align: left"
        >
          <Button icon="md-print">打印</Button>
          <Button icon="md-exit">导出</Button>
          <Button
            v-if="!isDisabled"
            style="float: right;margin-left: 8px"
            @click="handleCancel('formValidateType')"
          >
            取消
          </Button>
          <Button
            v-if="!isDisabled"
            style="float: right"
            type="primary"
            @click="handleConfirm('formValidateType')"
          >
            确认
          </Button>
        </p>
      </Modal>
      <!-- /弹出框 -->
    </Content>
    <!-- /table-content -->
  </div>
</template>

<script>
import Crumbs from '../components/Crumbs';

export default {
  name: 'ReplyList',
  components: { Crumbs },
  data() {
    return {
      // 设置是否选中行 默认为 false
      isSlectRow: false,
      // 控制筛选框显示隐藏  默认隐藏
      isScreen: false,
      // 筛选内容_字段
      screen: {
        name: '',
        ziJin: '',
        form: ''
      },
      // 表格内容 字段相对应
      tableContent: [
        {
          age: 1
        }
      ],
      // 弹出框内容显示隐藏   默认隐藏
      selTypeMod: false,
      // 控制 是否禁用  默认不禁用
      isDisabled: false,
      // 是否将弹层放置于 body 内  设置为true
      transferValue: true,
      // 添加表单的内容字段  ->  传递数据的字段
      formValidateType: {
        name: '',
        quality: '',
        remark: ''
      },
      // 表单内容的校验规则
      ruleValidateType: {
        name: [
          { required: true, message: '请填写机构名称', trigger: 'blur' }
        ],
        quality: [
          { required: true, message: '请选择机构性质', trigger: 'change' }
        ]
      }
    }
  },
  computed: {
    columns() {
      return [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center'
        },
        {
          title: '批复单位',
          key: 'age'
        },
        {
          title: '批复文号',
          key: 'age'
        },
        {
          title: '文件名称',
          key: 'age'
        },
        {
          title: '批复日期',
          key: 'age'
        },
        // TODO: 分三种 正在审 未通过 已通过
        {
          title: '审核状态',
          key: 'age'
        }
      ]
    }
  },
  methods: {
    // 子组件接收 控制筛选内容的隐藏和显示
    isShowScreen(msg) {
      this.isScreen = msg;
    },
    // 筛选确认按钮
    handleScreen() { },
    // 添加批复
    handleAdd() { },
    // 更新操作
    handleModify() { },
    // 打印操作
    handlePrint() { },
    // 导出操作
    handleExport() { },
    // 选中一行进行修改数据
    selectRow(currentRow, oldCurrentRow) {
      this.isSlectRow = true;
    },
    // 取消操作
    handleCancel() { },
    // 确认操作   关系后台数据
    handleConfirm() { }
  }
}
</script>

<style lang="scss" scoped>
</style>



